import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { FaStar, FaGoogle, FaChevronLeft, FaChevronRight, FaQuoteLeft } from 'react-icons/fa';

const GoogleReviews = () => {
    const [currentIndex, setCurrentIndex] = useState(0);
    const [direction, setDirection] = useState(0);
    const [isPaused, setIsPaused] = useState(false);

    // Google Reviews Data
    const reviews = [
        {
            id: 1,
            name: "Rajesh Kumar",
            rating: 5,
            date: "2 weeks ago",
            text: "Excellent financial planning services! The team at WeCare helped me plan my retirement effectively. Their personalized approach and regular follow-ups make them stand out. Highly recommended for anyone looking for professional wealth management.",
            avatar: "RK"
        },
        {
            id: 2,
            name: "Priya Sharma",
            rating: 5,
            date: "1 month ago",
            text: "I've been investing through WeCare for 3 years now. The returns have been consistent and the advisory is top-notch. They truly care about their clients' financial goals and provide honest, transparent advice.",
            avatar: "PS"
        },
        {
            id: 3,
            name: "Amit Patel",
            rating: 5,
            date: "3 weeks ago",
            text: "Best financial advisors in Gujarat! They helped me with tax planning and mutual fund investments. The team is very knowledgeable and always available to answer queries. Great experience overall!",
            avatar: "AP"
        },
        {
            id: 4,
            name: "Sneha Desai",
            rating: 5,
            date: "2 months ago",
            text: "WeCare Freedom Wealth has been managing my family's investments for over 5 years. Their systematic approach and regular portfolio reviews have helped us achieve our financial goals. Trustworthy and professional!",
            avatar: "SD"
        },
        {
            id: 5,
            name: "Vikram Singh",
            rating: 5,
            date: "1 week ago",
            text: "Impressed with their customer service and financial expertise. They helped me plan for my child's education and our dream home. The calculators on their website are very helpful too!",
            avatar: "VS"
        },
        {
            id: 6,
            name: "Meera Joshi",
            rating: 5,
            date: "3 months ago",
            text: "Professional, reliable, and client-focused. WeCare has helped me build a diversified portfolio that aligns with my risk appetite. Their regular updates and market insights are invaluable.",
            avatar: "MJ"
        }
    ];

    // Auto-rotate carousel with pause functionality
    useEffect(() => {
        if (!isPaused) {
            const timer = setInterval(() => {
                handleNext();
            }, 5000); // Change every 5 seconds

            return () => clearInterval(timer);
        }
    }, [currentIndex, isPaused]);

    const handleNext = () => {
        setDirection(1);
        setCurrentIndex((prevIndex) => (prevIndex + 1) % reviews.length);
    };

    const handlePrev = () => {
        setDirection(-1);
        setCurrentIndex((prevIndex) => (prevIndex - 1 + reviews.length) % reviews.length);
    };

    // Get visible reviews (previous, current, next)
    const getVisibleReviews = () => {
        const prevIndex = (currentIndex - 1 + reviews.length) % reviews.length;
        const nextIndex = (currentIndex + 1) % reviews.length;
        return [reviews[prevIndex], reviews[currentIndex], reviews[nextIndex]];
    };

    const visibleReviews = getVisibleReviews();

    const slideVariants = {
        enter: (direction) => ({
            x: direction > 0 ? 1000 : -1000,
            opacity: 0
        }),
        center: {
            zIndex: 1,
            x: 0,
            opacity: 1
        },
        exit: (direction) => ({
            zIndex: 0,
            x: direction < 0 ? 1000 : -1000,
            opacity: 0
        })
    };

    // Touch swipe handlers for mobile
    const [touchStart, setTouchStart] = useState(0);
    const [touchEnd, setTouchEnd] = useState(0);

    const handleTouchStart = (e) => {
        setTouchStart(e.targetTouches[0].clientX);
    };

    const handleTouchMove = (e) => {
        setTouchEnd(e.targetTouches[0].clientX);
    };

    const handleTouchEnd = () => {
        if (touchStart - touchEnd > 75) {
            // Swipe left
            handleNext();
        }

        if (touchStart - touchEnd < -75) {
            // Swipe right
            handlePrev();
        }
    };

    return (
        <section
            className="relative py-8 sm:py-12 lg:py-16"
            aria-label="Customer Reviews"
            role="region"
        >
            <div className="relative z-10 px-4 sm:px-6 lg:px-8">


                {/* Carousel Container */}
                <div
                    className="relative"
                    onMouseEnter={() => setIsPaused(true)}
                    onMouseLeave={() => setIsPaused(false)}
                >
                    {/* Desktop View - 3 Cards */}
                    <div className="hidden lg:grid lg:grid-cols-3 gap-6 xl:gap-8 items-center">
                        {visibleReviews.map((review, index) => {
                            const isCenter = index === 1;
                            return (
                                <motion.article
                                    key={review.id}
                                    initial={{ opacity: 0, scale: 0.8 }}
                                    animate={{
                                        opacity: isCenter ? 1 : 0.6,
                                        scale: isCenter ? 1.05 : 0.95,
                                        y: isCenter ? -10 : 0
                                    }}
                                    transition={{ duration: 0.5 }}
                                    className={`bg-white rounded-xl sm:rounded-2xl p-6 sm:p-8 shadow-lg border-2 transition-all duration-500 ${isCenter ? 'border-orange-400 shadow-2xl' : 'border-gray-200'
                                        }`}
                                    aria-hidden={!isCenter}
                                >
                                    {/* Quote Icon */}
                                    <FaQuoteLeft
                                        className={`text-2xl sm:text-3xl mb-3 sm:mb-4 ${isCenter ? 'text-orange-500' : 'text-gray-300'}`}
                                        aria-hidden="true"
                                    />

                                    {/* Review Text */}
                                    <p className="text-gray-700 mb-4 sm:mb-6 leading-relaxed line-clamp-4 text-sm sm:text-base">
                                        {review.text}
                                    </p>

                                    {/* Rating */}
                                    <div className="flex mb-3 sm:mb-4" role="img" aria-label={`${review.rating} out of 5 stars`}>
                                        {[...Array(review.rating)].map((_, i) => (
                                            <FaStar key={i} className="text-yellow-400 text-base sm:text-lg" aria-hidden="true" />
                                        ))}
                                    </div>

                                    {/* Author */}
                                    <div className="flex items-center">
                                        <div
                                            className={`w-10 h-10 sm:w-12 sm:h-12 rounded-full flex items-center justify-center text-white font-bold mr-3 sm:mr-4 text-sm sm:text-base ${isCenter ? 'bg-gradient-to-br from-orange-500 to-yellow-500' : 'bg-gray-400'
                                                }`}
                                            aria-hidden="true"
                                        >
                                            {review.avatar}
                                        </div>
                                        <div>
                                            <h4 className="font-bold text-gray-900 text-sm sm:text-base">{review.name}</h4>
                                            <p className="text-xs sm:text-sm text-gray-500">{review.date}</p>
                                        </div>
                                    </div>
                                </motion.article>
                            );
                        })}
                    </div>

                    {/* Mobile/Tablet View - Single Card with Touch Swipe */}
                    <div
                        className="lg:hidden"
                        onTouchStart={handleTouchStart}
                        onTouchMove={handleTouchMove}
                        onTouchEnd={handleTouchEnd}
                    >
                        <AnimatePresence initial={false} custom={direction} mode="wait">
                            <motion.article
                                key={currentIndex}
                                custom={direction}
                                variants={slideVariants}
                                initial="enter"
                                animate="center"
                                exit="exit"
                                transition={{
                                    x: { type: "spring", stiffness: 300, damping: 30 },
                                    opacity: { duration: 0.2 }
                                }}
                                className="bg-white rounded-xl sm:rounded-2xl p-6 sm:p-8 shadow-2xl border-2 border-orange-400 mx-auto max-w-2xl"
                                aria-live="polite"
                                aria-atomic="true"
                            >
                                <FaQuoteLeft className="text-2xl sm:text-3xl text-orange-500 mb-3 sm:mb-4" aria-hidden="true" />
                                <p className="text-gray-700 mb-4 sm:mb-6 leading-relaxed text-sm sm:text-base">
                                    {reviews[currentIndex].text}
                                </p>
                                <div className="flex mb-3 sm:mb-4" role="img" aria-label={`${reviews[currentIndex].rating} out of 5 stars`}>
                                    {[...Array(reviews[currentIndex].rating)].map((_, i) => (
                                        <FaStar key={i} className="text-yellow-400 text-base sm:text-lg" aria-hidden="true" />
                                    ))}
                                </div>
                                <div className="flex items-center">
                                    <div
                                        className="w-10 h-10 sm:w-12 sm:h-12 rounded-full bg-gradient-to-br from-orange-500 to-yellow-500 flex items-center justify-center text-white font-bold mr-3 sm:mr-4 text-sm sm:text-base"
                                        aria-hidden="true"
                                    >
                                        {reviews[currentIndex].avatar}
                                    </div>
                                    <div>
                                        <h4 className="font-bold text-gray-900 text-sm sm:text-base">{reviews[currentIndex].name}</h4>
                                        <p className="text-xs sm:text-sm text-gray-500">{reviews[currentIndex].date}</p>
                                    </div>
                                </div>
                            </motion.article>
                        </AnimatePresence>
                    </div>

                    {/* Navigation Buttons - Mobile optimized */}
                    <div className="flex justify-center items-center mt-6 sm:mt-8 space-x-3 sm:space-x-4">
                        <button
                            onClick={handlePrev}
                            className="w-10 h-10 sm:w-12 sm:h-12 bg-white rounded-full flex items-center justify-center shadow-lg hover:shadow-xl transition-all duration-300 hover:bg-orange-50 border-2 border-orange-200 touch-manipulation active:scale-95"
                            aria-label="Previous review"
                        >
                            <FaChevronLeft className="text-orange-600 text-sm sm:text-base" />
                        </button>

                        {/* Dots Indicator - Mobile responsive */}
                        <div className="flex space-x-1.5 sm:space-x-2" role="tablist" aria-label="Review navigation">
                            {reviews.map((_, index) => (
                                <button
                                    key={index}
                                    onClick={() => {
                                        setDirection(index > currentIndex ? 1 : -1);
                                        setCurrentIndex(index);
                                    }}
                                    className={`h-2.5 sm:h-3 rounded-full transition-all duration-300 touch-manipulation ${index === currentIndex
                                        ? 'bg-gradient-to-r from-orange-500 to-yellow-500 w-6 sm:w-8'
                                        : 'bg-gray-300 hover:bg-gray-400 w-2.5 sm:w-3'
                                        }`}
                                    aria-label={`Go to review ${index + 1}`}
                                    role="tab"
                                    aria-selected={index === currentIndex}
                                    aria-controls={`review-${index}`}
                                />
                            ))}
                        </div>

                        <button
                            onClick={handleNext}
                            className="w-10 h-10 sm:w-12 sm:h-12 bg-white rounded-full flex items-center justify-center shadow-lg hover:shadow-xl transition-all duration-300 hover:bg-orange-50 border-2 border-orange-200 touch-manipulation active:scale-95"
                            aria-label="Next review"
                        >
                            <FaChevronRight className="text-orange-600 text-sm sm:text-base" />
                        </button>
                    </div>
                </div>

                {/* Google Reviews Link - Mobile responsive */}
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    transition={{ delay: 0.3, duration: 0.8 }}
                    viewport={{ once: true }}
                    className="text-center mt-8 sm:mt-10 lg:mt-12"
                >
                    <a
                        href="https://g.page/r/CUFzgFupG-ycEAE/review"
                        target="_blank"
                        rel="noopener noreferrer"
                        className="inline-flex items-center bg-orange-600 text-white font-semibold py-2.5 sm:py-3 px-5 sm:px-6 rounded-full hover:bg-orange-700 transition-all duration-300 shadow-lg hover:shadow-xl touch-manipulation active:scale-95 text-sm sm:text-base"
                        aria-label="View all reviews on Google"
                    >
                        <FaGoogle className="mr-2" aria-hidden="true" />
                        View All Reviews on Google
                    </a>
                </motion.div>
            </div>
        </section>
    );
};

export default GoogleReviews;
